import React, { useState } from 'react';
import classNames from 'classnames';
import ReactAudio from 'react-h5-audio-player';
import { Link } from 'react-router-dom';
import audio1 from '../../audio/Test_01/L_0.mp3';
import audio2 from '../../audio/Test_02/R4_0.mp3';
import audio3 from '../../audio/Test_03/L5_-05.mp3';
import audio4 from '../../audio/Test_04/R6_-05.mp3';
import audio5 from '../../audio/Test_05/L7_-10.mp3';
import audio6 from '../../audio/Test_06/R8_-10.mp3';
import audio7 from '../../audio/Test_07/L1_-15.mp3';
import audio8 from '../../audio/Test_01/R_-15.mp3';
import audio9 from '../../audio/Test_02/L4_-20.mp3';
import audio10 from '../../audio/Test_03/R5_-20.mp3';
import audio11 from '../../audio/Test_04/L6_-25.mp3';
import audio12 from '../../audio/Test_05/R7_-25.mp3';
import audio13 from '../../audio/Test_06/L8_-30.mp3';
import audio14 from '../../audio/Test_07/R1_-30.mp3';
import audio15 from '../../audio/Test_01/L_-35.mp3';
import audio16 from '../../audio/Test_02/R4_-35.mp3';
import noAudio1 from '../../audio/Test_01/No_audio_1.mp3';
import noAudio2 from '../../audio/Test_01/No_audio_2.mp3';
import noAudio3 from '../../audio/Test_01/No_audio_3.mp3';
import noAudio4 from '../../audio/Test_01/No_audio_4.mp3';

// import './Test2.scss';

export const Test2:React.FC = () => {
  const test2 = [
    {
      audio: audio1,
      range: 0,
      options: ['Вважай', 'Врожай', 'Вгадай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio2,
      range: 0,
      options: ['Сховай', 'Чекай', 'Читай', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: audio3,
      range: -5,
      options: ['Не ламай', 'Нехай', 'Не чіпай', 'Не чую'],
      correct: 'Не чіпай',
      choice: '',
    },
    {
      audio: audio4,
      range: -5,
      options: ['Сідай', 'Тікай', 'Рушай', 'Не чую'],
      correct: 'Тікай',
      choice: '',
    },
    {
      audio: audio5,
      range: -10,
      options: ['Хапай', 'Рушай', 'Сідай', 'Не чую'],
      correct: 'Сідай',
      choice: '',
    },
    {
      audio: audio6,
      range: -10,
      options: ['Зривай', 'Зіграй', 'Не займай', 'Не чую'],
      correct: 'Зіграй',
      choice: '',
    },
    {
      audio: audio7,
      range: -15,
      options: ['Кидай', 'Китай', 'Копай', 'Не чую'],
      correct: 'Кидай',
      choice: '',
    },
    {
      audio: audio8,
      range: -15,
      options: ['Вмикай', 'Вдягай', 'Вважай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio9,
      range: -20,
      options: ['Чекай', 'Співай', 'Лягай ', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: audio10,
      range: -20,
      options: ['Не чіпай', 'Не ховай', 'Не тікай', 'Не чую'],
      correct: 'Не чіпай',
      choice: '',
    },
    {
      audio: audio11,
      range: -25,
      options: ['Лишай', 'Кидай', 'Тікай', 'Не чую'],
      correct: 'Тікай',
      choice: '',
    },
    {
      audio: noAudio1,
      range: 0,
      options: ['Дай', 'Грай', 'Знай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: noAudio2,
      range: 0,
      options: ['Читай', 'Чіпай', 'Чекай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio12,
      range: -25,
      options: ['Сховай', 'Сідай', 'Співай', 'Не чую'],
      correct: 'Сідай',
      choice: '',
    },
    {
      audio: audio13,
      range: -30,
      options: ['Зіграй', 'Джедай', 'Давай', 'Не чую'],
      correct: 'Зіграй',
      choice: '',
    },
    {
      audio: audio14,
      range: -30,
      options: ['Кусай', 'Копай', 'Кидай', 'Не чую'],
      correct: 'Кидай',
      choice: '',
    },
    {
      audio: noAudio3,
      range: 0,
      options: ['Пхай', 'Знай', 'Дбай', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
    {
      audio: audio15,
      range: -35,
      options: ['Хитай', 'Вдавай', 'Вважай', 'Не чую'],
      correct: 'Вважай',
      choice: '',
    },
    {
      audio: audio16,
      range: -35,
      options: ['Шукай', 'Чекай', 'Чіпай', 'Не чую'],
      correct: 'Чекай',
      choice: '',
    },
    {
      audio: noAudio4,
      range: 0,
      options: ['Випити', 'Вирити', 'Вимити', 'Не чую'],
      correct: 'Не чую',
      choice: '',
    },
  ];

  const [tests, setTests] = useState(test2);

  const [i, setI] = useState(0);

  const handleChoiceChange = (audio: string, choice: string) => {
    const newArr = [...tests].map((test) => {
      if (test.audio === audio) {
        if (test.choice.localeCompare(choice) !== 0) {
          test.choice = choice;
        } else {
          test.choice = '';
        }
      }

      return test;
    });

    setTests(newArr);
  };

  const [isFinished, setIsFinished] = useState(false);
  const [isFinishedQuestion, setIsFinishedQuestion] = useState(false);
  const [score, setScore] = useState(0);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [res, setRes] = useState('');

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const sumWithInitial = tests.reduce(
      (accumulator, cur) => {
        if (cur.choice === cur.correct) {
          return accumulator + 1;
        }

        return accumulator;
      },
      0,
    );
    const tempL = [10];
    const tempR = [10];

    for (const el of tests) {
      if (el.choice === el.correct && el.correct === 'L') {
        tempL.push(el.range);
      }

      if (el.choice === el.correct && el.correct === 'R') {
        tempR.push(el.range);
      }
    }

    setRes(`Ви чуєте лівим вухом при рівні ${Math.min(...tempL)} та правим ${Math.min(...tempR)} в залежності від звичайного голосу`);

    setScore(sumWithInitial);
    setIsFinished(true);
  };

  const handleQuestionChange = () => {
    if (i < tests.length - 1) {
      setIsFinishedQuestion(true);
      // setI(prev => prev + 1);
      setTimeout(() => {
        setI(prev => prev + 1);
        setIsFinishedQuestion(false);
      }, 100);
    }
  };

  return (
    <>
      <div className="progress-bar">
        <Link
          className="progress-bar__link"
          to="../"
        >
          &lt;&lt; Повернутися до тестів
        </Link>
        <h2 className="progress-bar__title">Тест 2</h2>
        <div className="progress-bar__progress">
          <div className="progress-bar__progress__level">
            <div className="progress-bar__progress__level__text">{`Питання ${i + 1}/${tests.length}`}</div>
            <div className="progress-bar__progress__level__container">
              <div className="progress-bar__progress__level__container__empty"></div>
              <div className={`progress-bar__progress__level__container__fill progress-bar__progress__level__container__fill--${
                Math.round(((i + 1) / tests.length) * 100)}`}
              >
              </div>
            </div>
          </div>
          <div className="progress-bar__progress__volume">
            <div className="progress-bar__progress__volume__text">Поточна гучність</div>
            <div className="progress-bar__progress__volume__container">
              <div className="progress-bar__progress__volume__container__empty "></div>
              <div className={`progress-bar__progress__volume__container__fill progress-bar__progress__volume__container__fill--${
                Math.round(((tests.length - i) / tests.length) * 100)}`}
              >
              </div>
            </div>
          </div>
        </div>
      </div>

      <form className="form" onSubmit={handleSubmit}>
        <div className="form_section">
          <h3 className="form_title">
            Визначіть поріг чутності звуків  кожним вухом окремо. Послухайте запис. Якщо нічого не почули, то позначте &quot;не чую&quot;, а якщо чуєте, то оберіть яким саме вухом почули слово. З наступними записами вчиніть аналогічно.
          </h3>
          <div
            className={classNames(
              'form_section__question',
              { form_section__question__wrong: tests[i].correct !== tests[i].choice && isFinishedQuestion },
              { form_section__question__right: tests[i].correct === tests[i].choice && isFinishedQuestion },
            )}
            key={tests[i].audio}
          >
            <div className="audio-player-container">
              <ReactAudio
                className="audioplayer"
                style={{ backgroundColor: 'aquamarine', color: 'red', width: '300px' }}
                src={tests[i].audio}
                layout="horizontal-reverse"
                showJumpControls={false}
                // showDownloadProgress={false}
                // showFilledProgress={false}
              />
            </div>

            {/* {i} */}

            <div className="form_section__question__answers">
              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[0]}`}
                  name={tests[i].audio}
                  value={tests[i].options[0]}
                  onChange={() => handleChoiceChange(tests[i].audio, tests[i].options[0])}
                  checked={tests[i].choice === tests[i].options[0]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[0]}`}>{tests[i].options[0]}</label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[1]}`}
                  name={tests[i].audio}
                  value={tests[i].options[1]}
                  onChange={() => handleChoiceChange(tests[i].audio, tests[i].options[1])}
                  checked={tests[i].choice === tests[i].options[1]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[1]}`}>{tests[i].options[1]}</label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[2]}`}
                  name={tests[i].audio}
                  value={tests[i].options[2]}
                  onChange={() => handleChoiceChange(tests[i].audio, tests[i].options[2])}
                  checked={tests[i].choice === tests[i].options[2]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[2]}`}>{tests[i].options[2]}</label>
              </div>

              <div>
                <input
                  type="radio"
                  id={`${tests[i].audio}_${tests[i].options[3]}`}
                  name={tests[i].audio}
                  value={tests[i].options[3]}
                  onChange={() => handleChoiceChange(tests[i].audio, tests[i].options[3])}
                  checked={tests[i].choice === tests[i].options[3]}
                  required
                />
                <label htmlFor={`${tests[i].audio}_${tests[i].options[3]}`}>{tests[i].options[3]}</label>
              </div>
            </div>
          </div>
        </div>
        {!isFinished && (
          <button
            type={i < tests.length - 1
              ? 'button'
              : 'submit'}
            className="button"
            onClick={handleQuestionChange}
          >
            {i < tests.length - 1
              ? 'Далі'
              : 'Закінчити тест'}
          </button>
        )}

        {isFinished && (
          <div className="result">
            <h2>Results</h2>
            <h3>{`Your score: ${score}`}</h3>
            {/* <p>{`Your result ${res}`}</p> */}
            {/* <p>{`Your detailed result ${res}`}</p> */}
          </div>
        )}

        {isFinished && (
          <Link
            className="section__test-card__link"
            to="../test-3"
            title="Перейти до тесту 3"
          >
            До тесту 3
          </Link>
        )}
      </form>
    </>
  );
};
