/* eslint-disable jsx-a11y/media-has-caption */
import React, { useState } from 'react';
import './Navigation.scss';
import classNames from 'classnames';
import { NavLink } from 'react-router-dom';
import logo from '../../images/logo.svg';
import menu from '../../images/menu.svg';
import cross from '../../images/cross.svg';

export const Navigation: React.FC = () => {
  const [menuIcon, setMenuIcon] = useState('burger');

  const handleMenuOpening = () => {
    if (menuIcon === 'burger') {
      setMenuIcon('cross');
    } else if (menuIcon === 'cross') {
      setMenuIcon('burger');
    }
  };

  return (
    <nav className={classNames(
      'nav',
      'menu',
      { 'menu--opened': menuIcon === 'cross' },
    )}
    >
      <div className="menu__container">
        <NavLink
          to="/"
          className="logo"
          title="Project Hear"
        >
          <img
            src={logo}
            alt="logo"
            className="logo__image"
          />
        </NavLink>
        <div className="menu__list">
          <NavLink
            to="/"
            className={
              ({ isActive }) => classNames(
                // 'navbar-item',
                'menu__list__item',
                { 'menu__list__item--is-active': isActive },
              )
            }
            title="Дізнайтесь більше про цей проект"
          >
            Про нас
          </NavLink>

          <NavLink
            className={
              ({ isActive }) => classNames(
                // 'navbar-item',
                'menu__list__item',
                { 'menu__list__item--is-active': isActive },
              )
            }
            to="/tests"
            title="Перейти до тестів"
          >
            Тести
          </NavLink>
          <NavLink
            className={
              ({ isActive }) => classNames(
                // 'navbar-item',
                'menu__list__item',
                { 'menu__list__item--is-active': isActive },
              )
            }
            to="/contacts"
            title="Перейти до контактів"
          >
            Контакти
          </NavLink>
        </div>
        <NavLink
          to="#"
          className="menu__icon menu__icon--burger"
          onClick={handleMenuOpening}
        >
          <img
            src={menuIcon === 'burger'
              ? menu
              : cross}
            alt="menu"
            className={classNames(
              'menu__image',
              'menu',
              { 'menu__image--cross': menuIcon === 'cross' },
            )}
          />
        </NavLink>
      </div>
      <div className={classNames(
        'menu__mobile',
        { 'menu__mobile--opened': menuIcon === 'cross' },
      )}
      >
        <NavLink
          to="/"
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          onClick={handleMenuOpening}
        >
          Home
        </NavLink>

        <NavLink
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          to="/tests/test-1"
          onClick={handleMenuOpening}
        >
          Test 1
        </NavLink>

        <NavLink
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          to="/tests/test-2"
          onClick={handleMenuOpening}
        >
          Test 2
        </NavLink>

        <NavLink
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          to="/tests/test-3"
          onClick={handleMenuOpening}
        >
          Test 3
        </NavLink>

        <NavLink
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          to="/tests/test-4"
          onClick={handleMenuOpening}
        >
          Test 4
        </NavLink>

        <NavLink
          className={
            ({ isActive }) => classNames(
              'navbar-item',
              { 'is-active': isActive },
            )
          }
          to="/tests/test-5"
          onClick={handleMenuOpening}
        >
          Test 5
        </NavLink>
      </div>
    </nav>
  );
};
